import React from "react";
import { Chrono } from "react-chrono";
import { items } from "./dataExperience";

function ExperienceSection() {
  return (
    <section className="site-section" id="Experience-section">
      <div className="container">
        <div className="row mb-5">
          <div className="col-12 position-relative">
            <h2
              className="section-title text-center mb-5"
              data-sal="slide-down"
              data-sal-delay="1400"
              data-sal-easing="ease"
            >
              My Experience
            </h2>
          </div>
        </div>
        <div
          className="row no-gutter"
          data-sal="slide-up"
          data-sal-delay="1400"
          data-sal-easing="ease"
        >
          <div className="col-12 position-relative">
            <div
              style={{ height: "65vh" }}
              className="section-title text-center"
            >
              <Chrono
                items={items}
                useReadMore={true}
                slideShow={false}
                hideControls
                // mode="HORIZONTAL"
                scrollable
                mode="VERTICAL_ALTERNATING"
                cardHeight={300}
                theme={{
                  primary: "#207561",
                  secondary: "blue",
                  // cardBgColor: "yellow",
                  cardForeColor: "black",
                  titleColor: "black",
                  titleColorActive: "white",
                }}
              />
            </div>
          </div>
        </div>
      </div>
    </section>
  );
}

export default ExperienceSection;
