import React from "react";
import ImageGallery from "react-image-gallery";
import "react-image-gallery/styles/css/image-gallery.css";
import { images } from "./dataCertificate";

function CertificateSection() {
  return (
    <section className="site-section bg-dark" id="certificate-section">
      <div
        className="container"
        data-sal="slide-down"
        data-sal-delay="1400"
        data-sal-easing="ease"
      >
        <div className="row">
          <div className="col-12 position-relative">
            <h2 className="section-title text-center mb-2 text-white">
              Certificate
            </h2>
          </div>
        </div>

        <ImageGallery items={images} showBullets={false} autoPlay={true} />
      </div>
    </section>
  );
}

export default CertificateSection;
