import React, { Fragment } from "react";
import { withPrefix } from "gatsby";
import { Helmet } from "react-helmet";
import { node } from "prop-types";
import "../../assets/fonts/icomoon/style.css";
import "../../assets/css/bootstrap.min.css";
// import "../../assets/css/animate.css";
import "../../assets/css/style.css";
// import mainJS from "../../assets/js/main.js";
// import easingJquery from "../../assets/js/jquery.easing.1.3";
// import stickyJquery from "../../assets/js/jquery.sticky";
// import bootstrapJS from "../../assets/js/bootstrap.min";

// import { WOW } from "wowjs";

// new WOW().init();

// const IMAGE_URL = `https://thekusuma.com/static/slide-6-4715e29302dbaa2ba21494c6258298d4.jpg`;
const META_DESCRIPTION = `M. Farchan A. Personal Website`;

function MainLayout({ children }) {
  return (
    <Fragment>
      <Helmet>
        <title>M. Farchan A.</title>

        {/* Favicon */}
        {/* <link rel="icon" type="image/png" href={Favicon} /> */}

        {/* font and SEO tags */}
        <meta
          name="viewport"
          content="width=device-width, initial-scale=1, shrink-to-fit=no"
        />
        <meta property="og:title" content="M. Farchan A. Personal Website" />
        {/* <meta property="og:image" content={IMAGE_URL} /> */}
        <meta property="og:url" content="https://mfarchana.github.io/" />
        <meta
          property="og:site_name"
          content="M. Farchan A. Personal Website"
        />
        <meta property="og:description" content={META_DESCRIPTION} />
        <meta name="twitter:title" content="M. Farchan A. Personal Website" />
        <meta name="twitter:description" content={META_DESCRIPTION} />
        {/* <meta name="twitter:image" content={IMAGE_URL} /> */}
        <meta name="twitter:url" content="https://mfarchana.github.io" />
        <meta name="twitter:card" content="summary" />
        <meta name="twitter:creator" content="@aanuser" />
        <link
          href="https://fonts.googleapis.com/css?family=Arbutus+Slab|Open+Sans:300,400,700"
          rel="stylesheet"
          type="text/css"
        />
        {/* <script src="https://code.jquery.com/jquery-3.6.0.min.js"></script> */}
        {/* <script src={bootstrapJS} type="text/javascript" />
        <script src={easingJquery} type="text/javascript" />
        <script src={stickyJquery} type="text/javascript" />
        <script src={mainJS} type="text/javascript" /> */}
      </Helmet>
      <body
        data-spy="scroll"
        data-target=".site-navbar-target"
        data-offset="300"
      >
        <div className="site-wrap">{children}</div>
      </body>
    </Fragment>
  );
}

MainLayout.propTypes = {
  children: node.isRequired,
};

export default MainLayout;
