import React from "react";
import Aan from "../../assets/images/aan.png";
import scrollTo from "gatsby-plugin-smoothscroll";

function WelcomeSection() {
  return (
    <>
      <div className="site-mobile-menu site-navbar-target">
        <div className="site-mobile-menu-header">
          <div className="site-mobile-menu-close mt-3">
            <span className="icon-close2 js-menu-toggle"></span>
          </div>
        </div>
        <div className="site-mobile-menu-body"></div>
      </div>

      <header
        className="site-navbar py-3 js-sticky-header site-navbar-target"
        role="banner"
      >
        <div className="container-fluid">
          <div className="row align-items-center justify-content-center">
            <div className="">
              <nav
                className="site-navigation position-relative text-right"
                role="navigation"
              >
                <ul className="site-menu main-menu js-clone-nav mr-auto d-none d-lg-block">
                  <li
                    data-sal="slide-up"
                    data-sal-delay="1250"
                    data-sal-easing="ease"
                  >
                    <a
                      // href="#home-section"
                      style={{ cursor: "pointer" }}
                      className="nav-link"
                      onClick={() => scrollTo("#home-section")}
                    >
                      Home
                    </a>
                  </li>
                  <li
                    data-sal="slide-up"
                    data-sal-delay="1300"
                    data-sal-easing="ease"
                  >
                    <a
                      // href="#about-section"
                      style={{ cursor: "pointer" }}
                      className="nav-link"
                      onClick={() => scrollTo("#about-section")}
                    >
                      About
                    </a>
                  </li>
                </ul>
              </nav>
            </div>

            <div className="text-center mx-5 ">
              <h1
                className="m-0 site-logo"
                data-sal="slide-up"
                data-sal-delay="1450"
                data-sal-easing="ease"
              >
                <a
                  // href="#find-me-section"
                  style={{ cursor: "pointer" }}
                  className="smoothscroll"
                  onClick={() => scrollTo("#find-me-section")}
                >
                  M. Farchan A.
                </a>
              </h1>
            </div>

            <div className="text-left">
              <nav
                className="site-navigation position-relative"
                role="navigation"
              >
                <ul className="site-menu main-menu js-clone-nav mr-auto d-none d-lg-block">
                  <li
                    data-sal="slide-up"
                    data-sal-delay="1350"
                    data-sal-easing="ease"
                  >
                    <a
                      // href="#Experience-section"
                      className="nav-link"
                      style={{ cursor: "pointer" }}
                      onClick={() => scrollTo("#Experience-section")}
                    >
                      Experience
                    </a>
                  </li>
                  <li
                    data-sal="slide-up"
                    data-sal-delay="1400"
                    data-sal-easing="ease"
                  >
                    <a
                      // href="#certificate-section"
                      className="nav-link"
                      style={{ cursor: "pointer" }}
                      onClick={() => scrollTo("#certificate-section")}
                    >
                      Certificate
                    </a>
                  </li>
                </ul>
              </nav>

              <div
                className="d-inline-block d-lg-none"
                style={{ position: "relative", top: "3px" }}
              >
                <a
                  href="#"
                  className="site-menu-toggle js-menu-toggle float-right"
                >
                  <span className="icon-menu h3"></span>
                </a>
              </div>
            </div>
          </div>
        </div>
      </header>

      <div className="site-blocks-cover overlay bg-light" id="home-section">
        <div className="container">
          <div className="row justify-content-center">
            <div className="col-md-12 mt-lg-5 text-left align-self-center text-intro">
              <div className="row">
                <div className="col-lg-6">
                  <div className="contents">
                    <h1
                      className="text-black"
                      data-sal="slide-right"
                      data-sal-delay="1600"
                      data-sal-easing="ease"
                    >
                      Hello !
                    </h1>
                    <p
                      className="lead"
                      data-sal="slide-right"
                      data-sal-delay="1900"
                      data-sal-easing="ease"
                    >
                      I'm Mochamad Farchan Ardiatama
                    </p>
                    <p
                      data-sal="slide-up"
                      data-sal-delay="11000"
                      data-sal-easing="ease"
                    >
                      <a
                        style={{ cursor: "pointer" }}
                        className="btn btn-primary active"
                        onClick={() => scrollTo("#about-section")}
                      >
                        About-Me
                      </a>
                    </p>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
        <img
          data-sal="fade"
          data-sal-delay="11500"
          // data-sal-easing="ease"
          src={Aan}
          alt="Image"
          className="img-face"
          loading="lazy"
        />
      </div>
    </>
  );
}

export default WelcomeSection;
